import React from "react";
import { ReactSVG } from "react-svg";
import { Link, useNavigate } from "react-router-dom";
import { useState } from "react";
import { utilService } from "../../services/util.service";
import { BiErrorCircle } from "react-icons/bi"
import { signup } from "../../store/user/user-action";
import { useDispatch } from "react-redux";

const Register = () => {
  const [isError, setIsError] = useState({
    errorType: '',
    isError: false
  })
  const [form, setForm] = useState({
    username: '',
    name: '',
    lastName: '',
    email: '',
    password: '',
    confirmPassword: '',
    phone: '',
  })

  const dispatch = useDispatch()
  const navigate = useNavigate()
  const passwordError = 'password'

  const handleChange = ({ target }) => {
    const { name, value } = target
    setForm((prevState) => ({ ...prevState, [name]: value }))
  }

  const onSubmit = async (ev) => {
    ev.preventDefault()
    if (!utilService.checkPasswordsMatch(form.password, form.confirmPassword)) {
      return setIsError((prevState) => ({ ...prevState, errorType: passwordError, isError: true }))
    }

    try {
      const res = await signup(dispatch, form)
      if (res) {
        navigate('/login')
      }
    } catch (err) {
      console.log('err', err)
    }
  }

  return (
    <div className="body-wrapper bg-color--gradient space-pt--70 space-pb--120">
      {/* auth page header */}
      <div className="auth-page-header space-mb--50">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <h3 className="auth-page-header__title">Welcome</h3>
              <p className="auth-page-header__text">
                Don't have account? <br /> Please sign up for creating a new
                account.
              </p>
            </div>
          </div>
        </div>
      </div>
      {/* auth page body */}
      <div className="auth-page-body">
        <div className="container">
          <div className="row">
            <div className="col-12">
              {/* Auth form */}
              <div className="auth-form">
                <form onSubmit={onSubmit}>
                  <div className="auth-form__single-field space-mb--30">
                    <label htmlFor="username">Username</label>
                    <input
                      type="text"
                      name="username"
                      id="username"
                      placeholder="Enter Username"
                      onChange={handleChange}
                      value={form.username}
                    />
                  </div>
                  <div className="auth-form__single-field space-mb--30">
                    <label htmlFor="name">First Name</label>
                    <input
                      type="text"
                      name="name"
                      id="name"
                      placeholder="Enter First Name"
                      onChange={handleChange}
                      value={form.name}
                    />
                  </div>
                  <div className="auth-form__single-field space-mb--30">
                    <label htmlFor="lastName">Last Name</label>
                    <input
                      type="text"
                      name="lastName"
                      id="lastName"
                      placeholder="Enter Last Name"
                      onChange={handleChange}
                      value={form.lastName}
                    />
                  </div>
                  <div className="auth-form__single-field space-mb--30">
                    <label htmlFor="emailAddress">Email Address</label>
                    <input
                      type="text"
                      name="email"
                      id="emailAddress"
                      placeholder="Enter Email Address"
                      onChange={handleChange}
                      value={form.email}
                    />
                  </div>
                  <div className="auth-form__single-field space-mb--30">
                    {isError.isError && isError.errorType === passwordError && <BiErrorCircle className="icon_s icon_error" />}
                    <label className={`${isError.isError ? "input_error" : ""}`} htmlFor="password">Password</label>
                    <input
                      type="password"
                      name="password"
                      id="password"
                      placeholder="Enter Password"
                      onChange={handleChange}
                      value={form.password}
                    />
                  </div>
                  <div className="auth-form__single-field space-mb--30">
                    <label htmlFor="confirmPassword">Confirm Password</label>
                    <input
                      type="password"
                      name="confirmPassword"
                      id="confirmPassword"
                      placeholder="Repeate Password"
                      onChange={handleChange}
                      value={form.confirmPassword}
                    />
                  </div>
                  <div className="auth-form__single-field space-mb--30">
                    <label htmlFor="phone">Phone Number</label>
                    <input
                      type="number"
                      name="phone"
                      id="phone"
                      placeholder="Enter Phone Number"
                      onChange={handleChange}
                      value={form.phone}
                    />
                  </div>
                  <div className="auth-form__single-field space-mb--40">
                    <p className="auth-form__info-text">
                      Already have an account?{" "}
                      <Link to={process.env.PUBLIC_URL + "/login"}>
                        Sign in Now
                      </Link>
                    </p>
                  </div>
                  <button className="auth-form__button">Sign Up</button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* auth page footer */}
      <div className="auth-page-footer">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <span className="auth-page-separator text-center space-mt--20 space-mb--20">
                - OR -
              </span>
              <div className="auth-page-social-login">
                <button>
                  <ReactSVG
                    src={
                      process.env.PUBLIC_URL + "/assets/img/icons/facebook.svg"
                    }
                  />
                  Sign In with Facebook
                </button>
                <button>
                  <ReactSVG
                    src={
                      process.env.PUBLIC_URL + "/assets/img/icons/google.svg"
                    }
                  />
                  Sign In with Google
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Register;
